@use 'settings/variables' as v;
@use 'tools/mixins' as m;

.c-raya {
    &__contenedor {
        @include m.flex (row, nowrap, flex-end, center);
        width: 90%;
        margin-top: 4rem;
    }
    &__raya {
        border: 2px solid #000;
        border-radius: 5px;
        padding-right: 6rem;
        margin-right: 5rem;
    }
}